import React, { useEffect } from "react";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader";
import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls.js";
import { OBJLoader } from "three/examples/jsm/loaders/OBJLoader.js";

let scene, renderer, camera;
let model, skeleton, mixer, clock;

const Obj2 = ({ takeScreenshotTrigger, character, setLoading }) => {
  function loadObjModel(url) {
    const loader = new OBJLoader();
    loader.crossOrigin = "anonymous";
    loader.load(
      url,
      // "https://gitlab.com/touchio/3d-model/-/raw/load_model/public/obj.obj?inline=false",
      // "http://firebasestorage.googleapis.com/v0/b/d-model-86d23.appspot.com/o/file%2Fobj.obj?alt=media&token=3c4d49fc-f62c-44fa-8604-06ba614ff9c0",
      function (gltf) {
        model = gltf;
        model.position.x = 0;
        model.position.y = character.selectedObject.configuration.groundY;
        model.position.z = 0;
        scene.add(model);

        model.traverse(function (object) {
          if (object.isMesh) object.castShadow = true;
        });

        skeleton = new THREE.SkeletonHelper(model);
        skeleton.visible = false;
        scene.add(skeleton);

        mixer = new THREE.AnimationMixer(model);

        animate();
      }
    );
  }
  function loadGLTFModel(url) {
    const loader = new GLTFLoader();
    loader.load(
      // "https://github.com/mrdoob/three.js/raw/dev/examples/models/gltf/Xbot.glb",
      // "https://github.com/Mugen87/dive/raw/master/app/models/soldier.glb",
      // "https://github.com/tattn/VRMKit/raw/master/Tests/Resources/AliciaSolid.vrm",
      url,
      function (gltf) {
        model = gltf.scene;
        model.rotation.y = character.selectedObject.configuration.roY;

        scene.add(model);

        model.traverse(function (object) {
          if (object.isMesh) object.castShadow = true;
        });

        skeleton = new THREE.SkeletonHelper(model);
        skeleton.visible = false;
        scene.add(skeleton);

        mixer = new THREE.AnimationMixer(model);

        animate();
      }
    );
  }
  function init() {
    const container = document.getElementById("model");
    clock = new THREE.Clock();

    scene = new THREE.Scene();
    scene.background = new THREE.Color(0x59472b);
    // scene.fog = new THREE.Fog(0xa0a0a0, 30, 150);

    const hemiLight = new THREE.HemisphereLight(0xffffff, 0x444444);
    hemiLight.position.set(0, 20, 0);
    scene.add(hemiLight);

    const dirLight = new THREE.DirectionalLight(0xffffff);
    dirLight.position.set(3, 10, 10);
    dirLight.castShadow = true;
    dirLight.shadow.camera.top = 2;
    dirLight.shadow.camera.bottom = -2;
    dirLight.shadow.camera.left = -2;
    dirLight.shadow.camera.right = 2;
    dirLight.shadow.camera.near = 0.1;
    dirLight.shadow.camera.far = 40;
    // scene.add(dirLight);
    // ground

    // const mesh = new THREE.Mesh(
    //   new THREE.PlaneBufferGeometry(10000, 10000),
    //   new THREE.MeshPhongMaterial({
    //     color: new THREE.Color("skyblue"),
    //     depthWrite: false,
    //   })
    // );
    // mesh.position.set(0, character.selectedObject.configuration.groundY, 0);
    // mesh.rotation.x = -Math.PI / 2;
    // mesh.receiveShadow = true;
    // scene.add(mesh);
    // console.log(character);

    const geometry = new THREE.PlaneBufferGeometry(100, 100);
    const planeMaterial = new THREE.MeshPhongMaterial({ color: 0xffb851 });

    const ground = new THREE.Mesh(geometry, planeMaterial);

    ground.position.set(0, character.selectedObject.configuration.groundY, 0);
    ground.rotation.x = -Math.PI / 2;
    ground.scale.set(100, 100, 100);

    ground.castShadow = false;
    ground.receiveShadow = true;

    scene.add(ground);

    if (character.selectedObject.type === "GLTFLoader") {
      const cubes1 = new THREE.Mesh(
        new THREE.BoxBufferGeometry(100, 1, 1),
        planeMaterial
      );

      cubes1.position.x = 0;
      cubes1.position.y = 0;
      cubes1.position.z = -5;

      cubes1.castShadow = true;
      cubes1.receiveShadow = true;

      scene.add(cubes1);

      const cubes2 = new THREE.Mesh(
        new THREE.BoxBufferGeometry(100, 2, 1),
        planeMaterial
      );

      cubes2.position.x = 0;
      cubes2.position.y = 0;
      cubes2.position.z = -6;

      cubes2.castShadow = true;
      cubes2.receiveShadow = true;

      scene.add(cubes2);

      loadGLTFModel(character.selectedObject.modelUrl);
    } else {
      const cubes1 = new THREE.Mesh(
        new THREE.BoxBufferGeometry(10000, 100, 100),
        planeMaterial
      );

      cubes1.position.x = 0;
      cubes1.position.y = character.selectedObject.configuration.groundY;
      cubes1.position.z = -300;

      cubes1.castShadow = true;
      cubes1.receiveShadow = true;

      scene.add(cubes1);

      const cubes2 = new THREE.Mesh(
        new THREE.BoxBufferGeometry(10000, 200, 100),
        planeMaterial
      );

      cubes2.position.x = 0;
      cubes2.position.y = character.selectedObject.configuration.groundY;
      cubes2.position.z = -400;

      cubes2.castShadow = true;
      cubes2.receiveShadow = true;

      scene.add(cubes2);

      loadObjModel(character.selectedObject.modelUrl);
    }
    // loadObjModel();
    renderer = new THREE.WebGLRenderer({ antialias: true });
    renderer.setPixelRatio(window.devicePixelRatio);
    renderer.setSize(window.innerWidth, window.innerHeight);
    renderer.outputEncoding = THREE.sRGBEncoding;
    renderer.shadowMap.enabled = true;
    container.appendChild(renderer.domElement);
    // camera

    camera = new THREE.PerspectiveCamera(
      character.selectedObject.configuration.fov,
      window.innerWidth / window.innerHeight,
      character.selectedObject.configuration.near,
      character.selectedObject.configuration.far
    );
    camera.position.set(
      character.selectedObject.configuration.x,
      character.selectedObject.configuration.y,
      character.selectedObject.configuration.z
    );
    // camera = new THREE.PerspectiveCamera(
    //   90,
    //   window.innerWidth / window.innerHeight,
    //   1,
    //   1150
    // );
    // camera.position.set(0, 50, 100);

    //man

    const controls = new OrbitControls(camera, renderer.domElement);
    controls.enablePan = false;
    controls.enableZoom = false;
    controls.target.set(0, 1, 0);
    controls.update();

    // stats = new Stats();
    // container.appendChild(stats.dom);

    window.addEventListener("resize", onWindowResize, false);
  }

  function onWindowResize() {
    camera.aspect = window.innerWidth / window.innerHeight;
    camera.updateProjectionMatrix();

    renderer.setSize(window.innerWidth, window.innerHeight);
  }

  function animate() {
    requestAnimationFrame(animate);

    // for (let i = 0; i !== numAnimations; ++i) {
    //   const action = allActions[i];
    //   const clip = action.getClip();
    //   const settings = baseActions[clip.name] || additiveActions[clip.name];
    //   settings.weight = action.getEffectiveWeight();
    // }

    // Get the time elapsed since the last frame, used for mixer update

    const mixerUpdateDelta = clock.getDelta();

    // Update the animation mixer, the stats panel, and render this frame

    mixer.update(mixerUpdateDelta);

    // stats.update();

    renderer.render(scene, camera);
  }

  useEffect(() => {
    init();
  }, []);

  function takeScreenshot() {
    setLoading(true);
    renderer.render(scene, camera);
    renderer.domElement.toBlob(
      function (blob) {
        var a = document.createElement("a");
        var url = URL.createObjectURL(blob);
        a.href = url;
        a.download = character.selectedObject.name + ".png";
        a.click();
      },
      "image/png",
      1.0
    );
    setLoading(false);
    // let name = "Loli" + Date.now();
    // renderer.domElement.toBlob(function (blob) {
    //   var metadata = {
    //     contentType: "image/png",
    //   };
    //   var image = new Image();
    //   image.src = blob;
    //   storageRef
    //     .child("image/" + name)
    //     .put(blob, metadata)
    //     .then((snapshot) => {
    //       snapshot.ref.getDownloadURL().then(function (downloadURL) {
    //         setLoading(false);
    //         updateCharacter({
    //           ...character.selectedObject,
    //           ...{
    //             imageUrl: downloadURL,
    //             // configuration: {
    //             //   ...character.selectedObject.configuration,
    //             //   ...{ roY: 0 },
    //             // },
    //           },
    //         });
    //       });
    //     });
    // });
  }

  useEffect(() => {
    if (takeScreenshotTrigger) takeScreenshot();
  }, [takeScreenshotTrigger]);

  return (
    <div
      style={{
        position: "absolute",
        zIndex: 0,
      }}
      id="model"
    />
  );
};
export default Obj2;
