import React, { useState } from "react";
import {
  SearchOutlined,
  CloseOutlined,
  LoadingOutlined,
} from "@ant-design/icons";
import "./styles.scss";

const SelectBox = ({ submidText, onChange, placeholder, loading }) => {
  const [text, setText] = useState(null);
  const handleKeyDown = (e) => {
    if (e.keyCode === 13) {
      if (submidText) submidText(text);
    }
  };
  const handleOnChange = (e) => {
    const value = e.target.value;
    setText(value);
    if (onChange) onChange(value);
  };
  const clearText = () => {
    setText("");
    if (onChange) onChange("");
  };
  return (
    <div className="container">
      <SearchOutlined className="search-icon" />
      <input
        className="input"
        onKeyDown={handleKeyDown}
        onChange={handleOnChange}
        value={text}
        placeholder={placeholder}
      />
      {!loading && text && text.length > 0 && (
        <CloseOutlined onClick={clearText} className="empty-icon" />
      )}
      {loading && <LoadingOutlined className="empty-icon" />}
    </div>
  );
};
export default SelectBox;
