export const search = async (searchValue = "") => {
  // await sleep(1500);
  const docData = await db
    // .orderBy("name")
    // .startAt(searchValue)
    // .endAt(`${searchValue}\uf8ff`)
    .get();
  const data = docData.docs
    .map((doc) => {
      return { ...doc.data(), ...{ id: doc.id } };
    })
    .filter(
      (item) =>
        item.name.toLowerCase().indexOf(searchValue.toLowerCase()) !== -1
    );
  return data;
};
export const updateCharacter = async (character) => {
  const id = character.id;
  delete character.id;
  console.log(id, character);
  await db.doc(id).update(character);
};
export const uploadFile = async () => {
  var file = new File([""], "Xbot.glb");
  storageRef
    .child("file/" + "Xbot.glb")
    .put(file)
    .then((snapshot) => {
      console.log("Uploaded.");
      snapshot.ref.getDownloadURL().then(function (downloadURL) {
        console.log(downloadURL);
      });
    });
};
