import React, { useState, useEffect } from "react";
import ObjectCard from "./ObjectCard";
import { Row, Col, Spin, Empty } from "antd";
import { search, uploadFile } from "../../../services/character";

import SelectBox from "../../common/SelectBox";
import "./styles.scss";

const Home = ({ navigation }) => {
  const [characters, setCharacters] = useState([]);
  const [filterValue, setFilterValue] = useState("");
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    callSearchCharacter();
    // saveFile("logo512.png");
    // getFile("logo512.png");
    uploadFile();
  }, []);

  const callSearchCharacter = async (searchValue) => {
    setLoading(true);
    try {
      const res = await search(searchValue);
      setCharacters(res);
    } finally {
      setLoading(false);
    }
  };

  const onSearch = (value) => {
    setFilterValue(value);
  };

  useEffect(() => {
    callSearchCharacter(filterValue);
  }, [filterValue]);

  return (
    <div>
      <Row style={{ padding: 20 }} alig="middle" justify="space-around">
        <Col xs={24} sm={24} md={12} lg={12} xl={12} xxl={12} span={12}>
          <SelectBox
            placeholder="Input a character name"
            allowClear
            onChange={onSearch}
            loading={loading}
          />
        </Col>
      </Row>
      {loading && (
        <Row alig="middle" justify="space-around">
          <Col>
            <Spin size="large" style={{ marginTop: 200 }} />
          </Col>
        </Row>
      )}
      {!loading && characters.length > 0 && (
        <Row>
          {characters.map((item) => (
            <Col
              style={{ padding: 20 }}
              xs={24}
              sm={24}
              md={12}
              lg={6}
              xl={6}
              xxl={6}
              span={6}
            >
              <div
                onClick={() => {
                  navigation.navigate("_3DObject", { selectedObject: item });
                }}
                style={{ width: "100%", height: "100%" }}
              >
                <ObjectCard imageUrl={item.imageUrl} title={item.name} />
              </div>
            </Col>
          ))}
        </Row>
      )}
      {!loading && characters.length === 0 && (
        <Row alig="middle" justify="space-around">
          <Col style={{ paddingTop: 150 }}>
            <Empty />
          </Col>
        </Row>
      )}
    </div>
  );
};

export default Home;
