import React, { useState, useEffect } from "react";
import "./App.css";
import Home from "./component/screens/Home";
import _3DObject from "./component/screens/_3DObject";

const navigation = {
  configuration: {
    initComponent: "Home",
  },
  components: [
    { key: "Home", component: Home, title: "Home" },
    { key: "_3DObject", component: _3DObject, title: "3D Model" },
  ],
};

function App() {
  const [currentComponent, setCurrentComponent] = useState({
    key: navigation.configuration.initComponent,
    data: {},
  });
  const navigate = (componentKey, data) => {
    setTitle(componentKey);
    setCurrentComponent({
      key: componentKey,
      data,
    });
  };
  const setTitle = (key) => {
    document.title = navigation.components.find(
      (component) => component.key === key
    ).title;
  };
  useEffect(() => {
    setTitle(currentComponent.key);
  }, []);
  return React.createElement(
    navigation.components.find(
      (component) => component.key === currentComponent.key
    ).component,
    { navigation: { navigate }, data: currentComponent.data }
  );
}

export default App;
